import axios from 'axios';
import request from "@/utils/request";

export default class UserService {
	getUsers() {
		return request({
			url: 'http://localhost:10000/user/all',
			method: 'get',
		})
		// return axios.get('http://localhost:10000/user/all').then(res => res.data.data);
    }
	saveUser(data) {
		return request({
			url: 'http://localhost:10000/user/save',
			method: 'post',
			data
		})
	}
	deleteUser(data) {
		return request({
			url: 'http://localhost:10000/user/delete',
			method: 'post',
			data
		})
	}
	deleteUserBatch(data) {
		return request({
			url: 'http://localhost:10000/user/deleteBatch',
			method: 'post',
			data
		})
	}
}
